<template>
  <div>
    <b-row>
      <b-col>
        <h2>Buyer Portal Project</h2>
      </b-col>
    </b-row>

    <b-carousel id="carousel"
                style="text-shadow: 1px 1px 2px #000;"
                controls
                indicators
                background="#ababab"
                :interval="4000"
                v-model="slide"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd">

      <b-carousel-slide v-for="image in buyerPortalImages" :key="image.id"
                        :text="image.description"
                        :img-src="image.src">
      </b-carousel-slide>
    </b-carousel>

    <b-row class="mt-5">
      <b-col>
        <h4>작업 내용</h4>
        <hr>
        <p>
          - Simple Global Customer Setting page Back-end, Front-end 작업<br>
          &nbsp;&nbsp;(GENERAL, NOTIFICATIONS, FEEDBACK, SOCIAL MEDIA, ANALYTICS, MESSAGING)
        </p>
      </b-col>
    </b-row>

    <b-row class="mt-2">
      <b-col>
        <h4>사용 기술</h4>
        <hr>
        <p>
          - HTML5<br>
          - CSS3<br>
          - Semantic-UI<br>
          - Javascript<br>
          - Vue.js<br>
          - SpringBoot<br>
          - MySQL<br>
          - JPA<br>
        </p>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'

  const {mapState} = createNamespacedHelpers('work');

  export default {
    name: "BuyerPortal",
    data() {
      return {
        slide: 0,
        sliding: null
      }
    },
    created() {
      this.passTheStatus('buyerPortal');
    },
    mounted() {
      this.$store.dispatch('work/getBuyerPortal')
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      }
    },
    computed: {
      ...mapState(['buyerPortalImages'])
    }
  }
</script>

<style scoped>
  h2 {
    color: #e67e22;
  }

  p {
    color: #bdc3c7;
  }
</style>